header{
    width: 100%;
    background: #122538;
    padding-top: 11px;
    margin: 0 auto;
    padding-left: 30px;
    display: flex;
    font-weight: 700;
    height: 70px;
    position: fixed;
    z-index: 40;
}
.LogoAndName{
    display: flex;
    margin-left: 100px;
}
.SiteName{
    color: white;
    font-weight: 700;
    font-size: 36px;
    text-decoration: none;
    letter-spacing: -2px;
    padding-top: 10px;
    padding-left: 10px;
}
.BasketName{
    position: absolute;
    right: 146px;
    display: flex;
    height: 70px;
}
.BasketIcon{
    padding-top: 10px;
    height: 50px;
    cursor: pointer;
}
.MadeBy{
    margin-right: 20px;
    font-size: 20px;
    padding-top: 35px;
}
.burger-btn {
    margin-top: 5px;
    width: 30px;
    height: 30px;
    margin-left: 20px;
    position: relative;
    cursor: pointer;
}
.BurgerImage {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background: #122538;
    transition: ease 0.15s;
}
.BurgerImage:active{
    background: #5f33b6;
    transition: ease 0.15s;
}
.burger-btn:before{
    content: "";
    position: absolute;
    top: 0;
    width: 30px;
}
.burger-btn:after{
    content: "";
    position: absolute;
    bottom: 0;
    width: 30px;
}
.BurgerImage *{
    fill: white;
}
.BasketButton{
    display: flex;
}
.basketLength{
    padding: 5px;
    margin-top: auto;
    margin-bottom: 5px;
    transition: ease 0.2s;
}
.basketLength.anim{
    border-radius: 50px;
    background-color: blueviolet;
    transition: ease 0.2s;
}
.changeCurrentCurrency{
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: 9px 22px 8px 12px; width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    margin: 15px;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: 8px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'), linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    background-position: right 11px top 50%, 0 0;
    background-size: 10px auto, 100%;
}